import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import en from './i18n/en.json';
import tw from './i18n/zh-tw.json';

const resources = {
    en: {
        translation: en,
    },
    'zh-TW': {
        translation: tw,
    },
};

const browserLng = window.navigator.language;

i18n.use(initReactI18next).init({
    resources,
    lng: browserLng,             //
    fallbackLng: 'en',     //
    interpolation: {
        escapeValue: false,
    },
});

export default i18n;